'use strict'

import SE from 'se'
import Promise from 'bluebird'

import { SE_CLIENT_ID, SE_KEY, SE_CHANNEL_URL } from '../config'

const init = new Promise((resolve, reject) => {
  SE.init({
    clientId: SE_CLIENT_ID,
    key: SE_KEY,
    channelUrl: SE_CHANNEL_URL,

    complete (data) {
      console.log('init', data)
      resolve(data)
    }
  })
})

export function authenticate () {
  let resolve
  let reject

  let promise = new Promise((iResolve, iReject) => {
    resolve = iResolve
    reject = iReject
  })

  SE.authenticate({
    success (data) {
      resolve(data)
    },

    error (data) {
      reject(data)
    }
  })

  return promise
}



